* {
	-webkit-box-sizing: border-box;
	box-sizing        : border-box
}
:after,
:before {
	-webkit-box-sizing: border-box;
	box-sizing        : border-box
}
html {
	font-size                  : 10px;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0)
}
body {
	font-family     : "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size       : 14px;
	line-height     : 1.42857143;
	color           : #333;
	background-color: #fff
}
button,
input,
select,
textarea {
	font-family: inherit;
	font-size  : inherit;
	line-height: inherit
}
a {
	color          : #337ab7;
	text-decoration: none
}
a:focus,
a:hover {
	color          : #23527c;
	text-decoration: underline
}
a:focus {
	outline       : thin dotted;
	outline       : 5px auto -webkit-focus-ring-color;
	outline-offset: -2px
}
figure {
	margin: 0
}
img {
	vertical-align: middle
}
.carousel-inner>.item>a>img,
.carousel-inner>.item>img,
.img-responsive,
.thumbnail a>img,
.thumbnail>img {
	display  : block;
	max-width: 100%;
	height   : auto
}
.img-rounded {
	border-radius: 6px
}
.img-thumbnail {
	display           : inline-block;
	max-width         : 100%;
	height            : auto;
	padding           : 4px;
	line-height       : 1.42857143;
	background-color  : #fff;
	border            : 1px solid #ddd;
	border-radius     : 4px;
	-webkit-transition: all .2s ease-in-out;
	transition        : all .2s ease-in-out
}
.img-circle {
	border-radius: 50%
}
hr {
	margin-top   : 20px;
	margin-bottom: 20px;
	border       : 0;
	border-top   : 1px solid #eee
}
.sr-only {
	position: absolute;
	width   : 1px;
	height  : 1px;
	padding : 0;
	margin  : -1px;
	overflow: hidden;
	clip    : rect(0, 0, 0, 0);
	border  : 0
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
	position: static;
	width   : auto;
	height  : auto;
	margin  : 0;
	overflow: visible;
	clip    : auto
}
